body .slick-next, body .slick-prev {
  background: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.4s ease;
  z-index: 99;
}

body .slick-next:before, body .slick-prev:before {
  color: #0E65AF;
  display: block;
  font-family: inherit;
  font-size: 30px;
  line-height: 1;
  margin: 0 auto;
  opacity: 1;
  position: absolute;
}

/*HOVER*/
.slick-prev:hover, .slick-prev:focus, .slick-next:hover, .slick-next:focus {
  background: white !important;
}

body .slick-prev {
  left: -20px;
}

body .slick-next {
  right: -20px;
}

body .slick-next,
body .slick-prev {
  background: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  height: 50px !important;
  top: 50%;
  transition: all 0.4s ease;
  width: 50px;
  z-index: 99;
  border-radius: 50%;
  display: flex !important;
  justify-content: center;
  align-items: center;
}

body .slick-prev:before {
  content: "❮" !important;
}

body .slick-next:before {
  content: "❯" !important;
}

body .slick-next:before,
body .slick-prev:before {
  color: #0E65AF;
  display: block;
  font-family: inherit;
  font-size: 30px;
  line-height: 1;
  margin: 0 auto;
  opacity: 1;
  position: absolute;
}

body .slick-next:focus,
body .slick-next:hover,
body .slick-prev:focus,
body .slick-prev:hover {
  background: #393939;
  opacity: 1;
}

@media screen and (max-width: 1210px) {
  body .slick-prev {
    left: 0;
  }
  body .slick-next {
    right: 0;
  }
  body .slick-next:before,
  body .slick-prev:before {
