.startseite-content-wrapper
    margin-top: 80px
    text-align: center

.startseite-content-wrapper .start-img-wrap
    position: relative !important
    border-radius: 8px
    overflow: hidden
    z-index: 2

.startseite-content-wrapper .start-img-wrap img
    width: 100%

.startseite-content-wrapper .start-img-wrap .overlay
    position: absolute !important
    left: 0
    bottom: 0
    width: 100%
    height: 100%
    background: rgb(0,0,0)
    background: linear-gradient(0deg, rgba(0,0,0,0.82) 21%, rgba(0,0,0,0) 100%)

.startseite-content-wrapper .img-content-wrap
    position: absolute !important
    left: 50px
    bottom: 50px
    z-index: 1
    text-align: left

.startseite-content-wrapper .img-content-wrap p
    color: white
    font-size: 18px

#angebot-teaser-wrap
    margin-top: 20px

#startseite-white-wrapper
    background: #fff
    position: relative
    padding: 250px 0 180px 0
    margin-top: -150px


@media (min-width: 1199px)
    #startseite-white-wrapper:after, #startseite-white-wrapper:before
        width: 5000px !important

#startseite-white-wrapper:before 
    right: 100%

#startseite-white-wrapper:after
    left: 100%

#startseite-white-wrapper:after, #startseite-white-wrapper:before
    background: #fff
    content: ""
    height: 100%
    position: absolute
    top: 0
    width: 12px


@media (max-width: 820px)
    .startseite-content-wrapper .start-img-wrap 
        height: 450px

    .startseite-content-wrapper .start-img-wrap img
        height: 100% !important
        object-fit: cover


/*ANGEBOTE*/

.artangebote_outer_frame .artangebote_image_frame
    height: 190px !important
    margin-top: 100px !important

.angebotswrapper .artangebote_outer_frame 
    width: calc(25% - 20px)

.artangebote_title_frame
    border-radius: 8px 8px 0 0
    font-size: 14px
    font-weight: 400
    line-height: 1.6
    padding: 10px
    top: 0
    left: 0
    width: 93%

.artangebote_outer_frame
    &:hover
        box-shadow: 0px 0px 0px 1px rgb(0 0 0 / 30%)
        border: none

.artangebote_outer_frame 
    &:hover .artangebote_title_frame
        background: $primary
        color: #fff

.artangebote_outer_frame
    border-radius: 8px

.artangebote_popup.artangebote_popup_right 
    border-left: none

.artangebote_popup
    height: 100% !important
    top: 0 !important
    box-shadow: rgba(0, 0, 0, 0.04) 0px 3px 5px
    border: none !important



@media (max-width: 768px)
    .startseite-wrapper .teaser-box-wrap
        grid-template-columns: repeat(2,48%)

    .angebotswrapper .artangebote_outer_frame 
        width: calc(50% - 20px)

    body .startseite-wrapper .slick-next, body .startseite-wrapper .slick-prev
        display: none !important


@media (max-width: 530px)
    .startseite-wrapper .teaser-box-wrap
        grid-template-columns: repeat(1,98%)

@media (max-width: 400px)
    .angebotswrapper .artangebote_outer_frame 
        width: calc(100% - 20px)

.contentBlock .row.header, .styledForm .row.header
    border-bottom: 2px solid $secondary !important